/**
 * WordPress dependencies
 */
import { _x, sprintf } from '@safe-wordpress/i18n';

/**
 * External dependencies
 */
import { DefaultAlternative } from '@nab/components';
import type { ExperimentType } from '@nab/types';

/**
 * Internal dependencies
 */
import icon from './icon.svg';
import { Original } from './components/original';
import { ValuePreview } from './components/value-preview';

import type { ControlAttributes, AlternativeAttributes } from './types';

export const settings: ExperimentType<
	ControlAttributes,
	AlternativeAttributes
> = {
	name: 'nab/javascript' as const,
	category: 'global',
	title: _x( 'JavaScript', 'text (experiment name)', 'nelio-ab-testing' ),
	description: _x(
		'Apply changes to your pages by running JS code',
		'user',
		'nelio-ab-testing'
	),
	labels: {
		create: _x(
			'Add new A/B test of JavaScript',
			'command',
			'nelio-ab-testing'
		),
	},
	icon,
	supports: {
		alternativeEdition: { type: 'external' },
		alternativePreviewDialog: ValuePreview,
		scope: 'urls',
	},
	checks: {
		getControlError: () => false,
		getAlternativeError: ( alternative, letter ) => {
			if ( ! alternative.code ) {
				return sprintf(
					/* translators: %s: Letter of the variant. */
					_x(
						'Please write some code in variant %s',
						'user',
						'nelio-ab-testing'
					),
					letter
				);
			}

			if ( -1 === alternative.code?.indexOf( 'done()' ) ) {
				return sprintf(
					/* translators: %s: Letter of the variant. */
					_x(
						'Please call function done() at least once in variant %s',
						'user',
						'nelio-ab-testing'
					),
					letter
				);
			}

			return false;
		},
	},
	help: {
		original: _x(
			'JavaScript split tests help you to improve the conversion rate of your WordPress site by running custom code to tweak the tested page(s). The control version (commonly known as the A version) wont run any code.',
			'user',
			'nelio-ab-testing'
		),
		alternative: _x(
			'Here you can create one or more JavaScript snippets that will be loaded when your pages DOM is ready. Click on the Edit link of each variant to view and edit its associated snippet.',
			'user',
			'nelio-ab-testing'
		),
	},
	defaults: {
		original: {},
		alternative: {
			name: '',
			code: '',
		},
	},
	views: {
		original: Original,
		alternative: DefaultAlternative,
	},
};
